@import "../../mediaqueries.scss";
@import "../../theme.scss";

.home-content {
  height: 100%;
  overflow-y: auto;

  

  .home {
    width: 1200px;
    height: 100%;
    margin: 0 auto;
    margin-top: 18px;

    .banner {
      position: relative;
      display: flex;
      background: rgba(255, 255, 255, 1);
      border-radius: 4px;
      padding: 0 45px 0 45px;

      .welcome-box {
        flex: 1;
        margin: 30px 0 22px 0;

        .top-welcome {
          h2 {
            height: 53px;
            font-size: 40px;
            color: rgba(0, 0, 0, 1);
            line-height: 53px;
            margin-bottom: 5px;

            span {
              margin-left: 19px;
              height: 49px;
              font-size: 40px;
              font-weight: normal;
              color: rgba(0, 0, 0, 1);
              line-height: 49px;
            }
          }
        }

        .middle-list {
          padding-left: 15px;

          ul {
            height: 78px;
          }

          ul li {
            float: left;
            padding: 7.5px 0;
            list-style: disc;
            margin-right: 40px;
          }

          ul li span {
            display: block;
            height: 24px;
            font-size: 18px;
            color: rgba(0, 0, 0, 1);
            line-height: 24px;
          }
        }

        .bottom-message {
          display: flex;
          margin-top: 35px;

          .cloud-size {
            width: 341px;
            position: relative;
            padding: 20px;
            border-radius: 4px;
            border: 1px solid rgba(216, 216, 216, 1);

            .cloud-size-top {
              display: flex;
              justify-content: space-between;

              .space {
                height: 19px;
                font-size: 14px;

                color: rgba(0, 0, 0, 1);
                line-height: 19px;
                margin-right: 16px;

                i {
                  color: #c5c5c5;
                  font-size: 16px;
                  margin-right: 8px;
                }

                .warning {
                  color: red;
                  font-size: 16px;
                  margin-right: 8px;
                }
              }

              .expansion-space {
                height: 19px;
                font-size: 14px;
                color: $bg-color;
                line-height: 19px;
                cursor: pointer;
              }
            }

            .size {
              margin: 8px 0;
            }

            .warning {
              span {
                height: 19px;
                font-size: 14px;
                color: rgba(0, 0, 0, 1);
                line-height: 19px;
              }
            }
          }

          .cloud-size-warning {
            border: 1px solid rgba(255, 17, 17, 1);
          }

          .bindframe-btn {
            position: relative;
            align-self: center;
            margin-left: 80px;

            span {
              position: relative;
              display: block;
              width: 188px;
              height: 50px;
              background: $bg-color;
              border-radius: 29px;
              border: 0;
              font-size: 15px;
              font-weight: 500;
              color: rgba(255, 255, 255, 1);
              line-height: 50px;
              text-align: center;
              cursor: pointer;
              z-index: 1;
              box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.3);
            }
          }

          .bindframe-btn::before {
            display: block;
            position: absolute;
            left: -40px;
            top: 50%;
            content: "";
            width: 1px;
            height: 48px;
            background: rgba(233, 233, 233, 1);
            transform: translateY(-50%);
          }

          .bindframe-btn::after {
            display: block;
            position: absolute;
            right: -42px;
            top: 50%;
            content: "";
            width: 1px;
            height: 48px;
            background: rgba(233, 233, 233, 1);
            transform: translateY(-50%);
          }
        }
      }

      .banner-img {
        width: 426px;
        margin: 13px 0;

        img {
          width: 100%;
          height: 100%;
        }
      }
    }

    .overall-data {
      margin: 24px 0;
      display: flex;
      justify-content: space-between;
    }

    .overview-inner {
      display: flex;
      width: 586px;
      background: rgba(255, 255, 255, 1);
      border-radius: 3px;
      border: 1px solid rgba(255, 255, 255, 1);
      cursor: pointer;

      
      .overview-left {
        flex: 1;

        p:nth-child(1) {
          display: flex;
          justify-content: center;
          margin-top: 32px;

          .top_icon{
            display: flex;
            justify-content: center;
            align-items: center;
            width: 70px;
            height: 70px;
            border-radius: 50%;
            text-align: center;

            span{
              display: block;
              width: 35px;
              height: 35px;
            }
          }
        }

        p:nth-child(2) {
          text-align: center;
          height: 47px;
          font-size: 40px;
          font-weight: 500;
          color: rgba(0, 0, 0, 1);
          line-height: 47px;
          margin: 36px 0 20px 0;
        }

        p:nth-child(3) {
          text-align: center;
          height: 27px;
          font-size: 18px;
          font-weight: 400;
          color: rgba(0, 0, 0, 1);
          line-height: 27px;
          margin-bottom: 30px;
        }
      }

      .overview-right {
        position: relative;
        width: 308px;
        display: flex;
        flex-wrap: wrap;
        padding: 16px 0 10px 0;

        .imgbox {
          position: relative;
          width: 138px;
          height: 107px;
          border-radius: 4px;
          margin-right: 16px;
          background-image: url('../../images/imgerror.png');
          background-repeat: no-repeat;
          background-position: center center;
          background-size: 100% 100%;
          box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.3);
          overflow: hidden;

          img {
            position: absolute;
            top: 50%;
            left: 50%;
            display: block;
            min-width: 100%;
            min-height: 100%;
            transform: translate(-50%, -50%);
          }
        }

        i {
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
        }
      }

      .home-frame {
        width: 308px;
        display: flex;
        align-items: center;
        justify-content: center;

        .frameimgbox {
          width: 140px;
          height: 174px;
          margin-right: 12px;
          border-radius: 4px;
          border: 7px solid #fff;
          box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.3);

          img {
            width: 100%;
            height: 100%;
            border-radius: 4px;
          }
        }
      }

      &:hover {
        box-shadow: 0px 4px 12px 0px rgba(0, 0, 0, 0.2);
        border: 1px solid $bg-color;
      }
    }

    .download-frames {
      display: flex;
      justify-content: space-between;
      margin-bottom: 24px;

      .download-app,
      .email-set {
        width: 586px;
        background: rgba(255, 255, 255, 1);
        border-radius: 3px;
        padding-top: 15px;

        .download-app-head,
        .email-set-head {
          margin-bottom: 10px;

          span {
            display: block;
            height: 29px;
            font-size: 20px;
            font-weight: 500;
            color: rgba(0, 0, 0, 1);
            line-height: 29px;
            padding-left: 15px;
          }
        }

        .download-app-boby {

          .download-app-explain {
            padding-left: 15px;

            .text {
              font-size: 15px;
              font-weight: 400;
              color: rgba(0, 0, 0, 1);
              line-height: 22px;
            }

            .app-qr {
              display: flex;
              justify-content: space-around;
              margin-top: 5px;
              .apple,
              .google,
              .download {
                .qr {
                  text-align: center;
                }

                .img-icon {
                  display: block;
                  width: 130px;
                  text-align: center;

                  img {
                    width: 100%;
                  }
                }
              }
            }
          }
        }
      }
    }
    .iconfont{
      font-size: 30px;
    }
  }
}